import React from 'react';
import { Link } from 'react-router';
import FabricSelector from './FabricSelector';
import { Wrapper, Block, Title2, Button3, Logo, Question, Center, Header2, ProgressBar, Progress, ProgressActive, ProgressDot, ProgressText } from './styledComponents';
import LogoImg from '../assets/logo1.png';

const Step3 = () => (
  <Wrapper>
    <Block>
      <Header2>
        <Logo src={LogoImg} />
        <ProgressBar>
          <Progress>
            <ProgressDot>
              1
            </ProgressDot>
            <ProgressText>
              Vetements
            </ProgressText>
          </Progress>
          <ProgressActive>
            <ProgressDot>
              2
            </ProgressDot>
            <ProgressText>
              Retouches
            </ProgressText>
          </ProgressActive>
          <Progress>
            <ProgressDot>
              3
            </ProgressDot>
            <ProgressText>
              Mon RDV
            </ProgressText>
          </Progress>
        </ProgressBar>
        <Title2>
          Mes retouches
        </Title2>
      </Header2>
      <Question>Afin de vous attribuer le couturier le plus adapté à vos besoins, merci de préciser si votre commande
        comporte :</Question>
      <FabricSelector />
      <Center><Link to={'/order/step4'}><Button3>{'> Valider <'}</Button3></Link></Center>
    </Block>
  </Wrapper>
);

export default Step3;



